import {Component} from "@angular/core";
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";
import {ListOperationalHours} from "@youtap-web-lib";
import * as dayjs from "dayjs";
import "dayjs/locale/id";
import {WindowActionService} from "../../../shared/service/window-action.service";

dayjs.locale("id");

@Component({
  selector: "supplier-info-modal",
  templateUrl: "./supplier-info-modal.component.html"
})
export class SupplierInfoModalComponent extends Modal {
  title!: string;
  showButton = false;
  supplierInfo?: ListOperationalHours[];
  phoneSupplier!: string;
  minPurchase!: number;
  emailSupplier!: string;
  imageSupplier!: string;
  nameSupplier!: string;
  supplierId!: string;
  tierImage!: string;

  today = Number(dayjs().locale("id").format("d"));

  constructor(
    private windowActionService: WindowActionService
  ) {
    super();
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
    this.supplierInfo = inputs.data.supplierInfo as ListOperationalHours[];
    this.phoneSupplier = inputs.data.phoneSupplier;
    this.emailSupplier = inputs.data.emailSupplier;
    this.nameSupplier = inputs.data.nameSupplier;
    this.imageSupplier = inputs.data.imageSupplier;
    this.supplierId = inputs.data.supplierId;
    this.minPurchase = inputs.data.minPurchase;
    this.tierImage = inputs.data.tierImage;
  }

  select(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({data: "1234"});
  }

  openEmail() {
    this.windowActionService.href('mailto:' + this.emailSupplier)
  }
}
