import {Component, OnInit} from "@angular/core";
import {ActivatedRoute} from "@angular/router";
import {
  SupplierService,
  WarehouseService,
  CoverageAreaService,
  ListCoverageAreaInput, DetailWarehouseInput, ResponseDetailWarehouseDto, ICoverageAreaResponse, CoverageAreas
} from "@youtap-web-lib";
import * as dayjs from "dayjs";
import "dayjs/locale/id";

dayjs.locale("id");

@Component({
  selector: "youtap-bos-coverage-area",
  templateUrl: "./coverage-area.component.html"
})
export class CoverageAreaComponent implements OnInit {
  constructor(
    private supplierService: SupplierService,
    private warehoiseService: WarehouseService,
    private route: ActivatedRoute,
    private coverageAreaService: CoverageAreaService
  ) {
  }

  loading = false;
  listCoverageArea!: ICoverageAreaResponse;
  detailWarehouse!: ResponseDetailWarehouseDto
  today = Number(dayjs().locale("id").format("d"));
  coverages: CoverageAreas[] = [];
  loadMore = true;
  warehouseId!: string | null;
  page = 0;

  ngOnInit(): void {
   const id = this.route.snapshot.paramMap.get("id");
   this.warehouseId = id;
    if (id) {
      this.getCoverageBySupplierId(id);
      this.getDetailWarehouse(id)
    }
  }


  getDetailWarehouse(id: string) {
    const getData: DetailWarehouseInput = {
      detailWarehouseInput: {
        id: id
      }
    }
    this.warehoiseService.detailWarehouse(getData).subscribe({
      next: ({data}) => {
        this.detailWarehouse = data
      }
    })
  }



  getCoverageBySupplierId(id: string | null) {
    this.loading = true;
    this.loadMore = true;
    const data: ListCoverageAreaInput = {
      listCoverageAreaInput: {
        limit: 25,
        page: this.page,
        warehouseId: id
      }
    };

    this.coverageAreaService.listCoverageArea(data).subscribe({
      next: ({data}) => {
        this.loading = false;
        this.listCoverageArea = data.listCoverageArea;
        data.listCoverageArea.coverageAreas.forEach((coverage) => {
          this.coverages.push(coverage);
        });

        if (this.page === data.listCoverageArea.totalPage) {
          this.loadMore = false;
        }
      },
      error: (err) => {
        err;
        this.loading = false;
      }
    });
  }

  onScroll() {
    this.page += 1;
    if (this.loadMore && this.page <= this.listCoverageArea.totalPage) {
      this.getCoverageBySupplierId(this.warehouseId);
    }
  }
}
