import { Component } from '@angular/core';
import { Modal } from '../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../shared/service/modal/modal.service';
import { Category, CategoryService, ListCategoryDto } from '@youtap-web-lib';

@Component({
  selector: 'category-modal',
  templateUrl: './category-modal.component.html'
})
export class CategoryModalComponent extends Modal {
  title!: string;
  showButton = false;
  categoryList: Category[] = [];
  nameLabel = 'Semua';
  isLoading = false;

  constructor(private categoryService: CategoryService) {
    super();
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
    this.categoryList = inputs.data.categoryList;
    this.nameLabel = inputs.data.nameLabel ? inputs.data.nameLabel : 'Semua';
    if (!this.categoryList) {
      this.getListCategory(inputs.data['supplierId']);
    }
  }

  getListCategory(id: string) {
    this.isLoading = true;
    const data: ListCategoryDto = {
      listCategoryInput: {
        page: 1,
        limit: 25,
        filter: {
          supplierId: id
        }
      }
    };
    this.categoryService.listCategory(data).subscribe({
      next: ({ data }) => {
        this.categoryList = data.listCategory.categories;
        this.isLoading = false;
      },
      error: () => {
        this.isLoading = false;
      }
    });
  }

  select(value: Category): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({ data: '1234' });
  }
}
