import {
  Component,
  Inject,
  OnDestroy,
  OnInit,
  PLATFORM_ID
} from '@angular/core';
import { ActivatedRoute, Router, Scroll } from '@angular/router';
import {
  Location,
  ViewportScroller,
  isPlatformBrowser,
  isPlatformServer
} from '@angular/common';
import { filter, Subscription } from 'rxjs';
import * as dayjs from 'dayjs';
import 'dayjs/locale/id';

import {
  WarehouseService,
  ProductService,
  CategoryService,
  ListCategoryDto,
  ResponseListCategoryDto,
  Category,
  ResponseDetailWarehouseDto,
  DetailWarehouseInput,
  ListProductDto,
  Products,
  ResponseListProductDto,
  IsSupplierCoveredDto,
  SupplierService,
  ProductVariantOrderByTypes,
  ImageSizeTypes,
  SortByTypes
} from '@youtap-web-lib';

import { AuthData } from '../../../shared/data/auth.data';
import { ScrollData } from '../../../shared/data/scroll.data';

import { MetaService } from '../../../shared/service/meta.service';
import { ReferralService } from '../../../shared/service/referral.service';
import { GaService } from '../../../shared/service/ga.service';
import { ModalService } from '../../../shared/service/modal/modal.service';

import { PriceSortModalComponent } from '../../home/price-sort-modal/price-sort-modal.component';
import { CategoryModalComponent } from '../../home/category-modal/category-modal.component';
import { SupplierInfoModalComponent } from '../supplier-info-modal/supplier-info-modal.component';
import { TransferStateService } from '../../../shared/service/transfer-state.service';

dayjs.locale('id');
const STATE_KEY_SUPPLIER = 'dataSupplier';

@Component({
  selector: 'youtap-bos-supplier-detail',
  templateUrl: './supplier-detail.component.html',
  styleUrls: ['./supplier-detail.component.scss']
})
export class SupplierDetailComponent implements OnInit, OnDestroy {
  warehouseDetail!: ResponseDetailWarehouseDto;
  listProduct!: ResponseListProductDto;
  listCategory!: ResponseListCategoryDto;
  subscription!: Subscription;
  products: Products[] = [];
  listCategorys: Category[] = [];
  today = Number(dayjs().locale('id').format('d'));
  scrollPosition!: [number, number] | null;
  page = 1;
  statePage = 0;
  isCovered!: boolean;
  display!: boolean;
  category!: boolean;
  iSupplier!: boolean;
  isLoading = true;
  isActive = true;
  loading = false;
  canLoad = true;
  isHidden = true;
  isReady = false;
  beranda = true;
  promo = false;
  promoDiscount: boolean = false;
  categoryId!: string;
  supplierSlug!: string;
  slug!: string;
  search!: string;
  refCode!: string;
  token = '';
  filterPriceSort = 'lowerPrice';
  nameLabel = 'Semua';
  emptyStateProduct: boolean = false;
  emptyDetailWharehouse: boolean = false;
  // races: Array<RaceModel>;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private warehouseService: WarehouseService,
    private productService: ProductService,
    private modalService: ModalService,
    private categoryService: CategoryService,
    private route: ActivatedRoute,
    private router: Router,
    public authData: AuthData,
    private location: Location,
    private metaService: MetaService,
    private supplierService: SupplierService,
    private viewportScroller: ViewportScroller,
    private scrollData: ScrollData,
    private referralService: ReferralService,
    private gaService: GaService,
    private transferStateService: TransferStateService
  ) {
    this.initScrollPosition();
    // this.viewportScroller.scrollToPosition([0, 789])
  }

  ngOnInit(): void {
    if (isPlatformBrowser(this.platformId)) {
      this.gaService.addEvent('Supplier_detail', 'supplier', '');
      this.token = this.authData.token;
      this.isReady = true;
      this.display = false;
      this.category = false;
      this.iSupplier = false;
    }
    this.checkParams();
  }

  initScrollPosition() {
    this.subscription = this.router.events
      .pipe(filter((e) => e instanceof Scroll))
      .subscribe((e) => {
        if ((e as Scroll).position !== null) {
          this.scrollPosition = (e as Scroll).position;
          if (this.scrollPosition) {
            this.viewportScroller.scrollToPosition(this.scrollPosition);
          }
        } else {
          this.scrollPosition = [0, 0];
        }
      });
  }

  checkParams() {
    this.refCode = this.route.snapshot.queryParams['refcode'];
    const dataId = this.route.snapshot?.queryParamMap.get('s');
    const supplierSlug = this.route.snapshot?.queryParamMap.get('ss');
    const category =
      this.route.snapshot?.queryParamMap.get('category') || undefined;
    this.search = this.route.snapshot?.queryParamMap.get('search') || '';

    if (supplierSlug && this.refCode) {
      this.referralService.setReferral(supplierSlug, this.refCode, 'supplier');
    }

    if (dataId) {
      this.slug = dataId;
      this.checkState();
      this.getDetailWarehouse(category);
      if (category) {
        this.categoryId = category;
      }
    } else if (supplierSlug) {
      this.supplierSlug = supplierSlug;
      this.checkState();
      this.getDetailWarehouse(category);
    }
  }

  checkState() {
    if (
      this.scrollData.supplierState &&
      this.scrollData.supplierState.supplierSlug === this.supplierSlug
    ) {
      this.statePage = this.scrollData.supplierState.page;
    }
  }

  ngOnDestroy() {
    this.scrollData.supplierState = {
      page: this.page,
      supplierSlug: this.supplierSlug
    };
    this.subscription.unsubscribe();
  }

  searchChange(search: string) {
    this.page = 1;
    this.products = [];
    const category =
      this.route.snapshot?.queryParamMap.get('category') || undefined;
    this.search = search;
    this.getListProduct(this.warehouseDetail.detailWarehouse.id, category);
  }

  fetchDataDetailWarehouse(isServer = false, category?: string) {
    const dataWarehouse: DetailWarehouseInput = {
      detailWarehouseInput: {
        ...(this.slug && { slug: this.slug }),
        ...(this.supplierSlug && { supplierSlug: this.supplierSlug })
      }
    };

    this.warehouseService.detailWarehouse(dataWarehouse).subscribe({
      next: ({ data }) => {
        this.loading = false;
        if (isServer) {
          this.transferStateService.set(STATE_KEY_SUPPLIER, data);
        }
        if (data.detailWarehouse) {
          this.emptyDetailWharehouse = false;
          this.warehouseDetail = data;
          this.metaService.setMeta({
            title: `${data.detailWarehouse.name} - Produk Terlengkap Harga Kompetitif | Youtap BOS`,
            description: `Beli produk ${data.detailWarehouse.name} terlengkap dan harga kompetitif. Belanja Online Kebutuhan Usaha Anda hanya di Youtap BOS`,
            image: data.detailWarehouse.supplier.image
          });
          this.getListProduct(data.detailWarehouse.id, category);
          this.getListCategory(data.detailWarehouse.supplier.id, category);
          // this.getOperationalHours(data.detailWarehouse.supplier.id);
          this.isSupplierCovered(data.detailWarehouse.supplier.id);
        } else {
          this.emptyDetailWharehouse = true;
        }
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getDetailWarehouse(category?: string) {
    this.loading = true;
    if (isPlatformServer(this.platformId)) {
      this.fetchDataDetailWarehouse(true, category);
    }
    if (isPlatformBrowser(this.platformId)) {
      this.loading = false;
      const data = this.transferStateService.get<ResponseDetailWarehouseDto>(
        STATE_KEY_SUPPLIER,
        {} as ResponseDetailWarehouseDto
      );
      if (data && data.detailWarehouse) {
        this.emptyDetailWharehouse = false;
        this.warehouseDetail = data;
        this.metaService.setMeta({
          title: `${data.detailWarehouse.name} - Produk Terlengkap Harga Kompetitif | Youtap BOS`,
          description: `Beli produk ${data.detailWarehouse.name} terlengkap dan harga kompetitif. Belanja Online Kebutuhan Usaha Anda hanya di Youtap BOS`,
          image: data.detailWarehouse.supplier.image
        });
        this.getListProduct(data.detailWarehouse.id, category);
        this.getListCategory(data.detailWarehouse.supplier.id, category);
        // this.getOperationalHours(data.detailWarehouse.supplier.id);
        this.isSupplierCovered(data.detailWarehouse.supplier.id);
      } else {
        this.fetchDataDetailWarehouse(false, category);
      }
    }
  }

  isSupplierCovered(id: string) {
    this.isLoading = true;
    const data: IsSupplierCoveredDto = {
      supplierId: id
    };

    this.supplierService.isSupplierCovered(data).subscribe({
      next: ({ data }) => {
        this.isLoading = false;
        this.isCovered = data?.isSupplierCovered;
      },
      error: (error) => {
        this.isLoading = false;
        return error;
      }
    });
  }

  getListProduct(id: string | null, categoryId?: string) {
    this.loading = true;
    const referral = this.referralService.getReferral(this.supplierSlug);
    const data: ListProductDto = {
      listProductInput: {
        page: this.page,
        limit: 25,
        filter: {
          warehouseId: id,
          categoryId: categoryId,
          hasActiveDiscount: this.promo,
          ...(referral && { supplierReferralCode: `${referral.referral}` })
        },
        order: {
          orderBy: ProductVariantOrderByTypes.PRICE,
          sortBy:
            this.filterPriceSort === 'lowerPrice'
              ? SortByTypes.ASC
              : SortByTypes.DESC
        },
        mutator: {
          imageResolution: ImageSizeTypes.M
        },
        search: {
          keyword: this.search
        }
      },
      referralCode: referral ? referral.referral : '',
      warehouseId: id
    };
    this.productService.listProduct(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        if (data.listProduct.products.length === 0 && this.page === 1) {
          this.emptyStateProduct = true;
        }
        data.listProduct.products.forEach((product) => {
          const productVariant = product.productVariants.filter(
            (variant) => variant.stock > 0
          );
          const prod: Products = {
            actualPriceMax: product.actualPriceMax,
            actualPriceMin: product.actualPriceMin,
            description: product.description,
            id: product.id,
            image: product.image,
            isActive: product.isActive,
            isPODAvailable: product.isPODAvailable,
            name: product.name,
            activeDiscount: product.activeDiscount,
            productVariants: productVariant.length
              ? productVariant
              : product.productVariants,
            slug: product.slug,
            supplier: product.supplier,
            warehouse: product.warehouse,
            categoryProducts: product.categoryProducts,
            isAvailable: product.isAvailable
          };
          this.products.push(prod);
        });
        if (!data.listProduct.products.length) {
          this.canLoad = false;
        }
        if (this.statePage && this.page <= this.statePage) {
          this.onScroll();
          if (this.scrollPosition) {
            this.viewportScroller.scrollToPosition(this.scrollPosition);
          }
        } else if (this.statePage !== 0) {
          if (this.scrollPosition) {
            this.viewportScroller.scrollToPosition(this.scrollPosition);
          }
        }
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getListCategory(id: string, categoryId?: string) {
    this.loading = true;
    const data: ListCategoryDto = {
      listCategoryInput: {
        limit: 20,
        page: 1,
        filter: {
          supplierId: id
        }
      }
    };
    this.categoryService.listCategory(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.listCategorys = data.listCategory.categories;
        if (categoryId) {
          this.nameLabel = this.listCategorys.filter(
            (item) => item.id === categoryId
          )[0].name;
        }
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  priceSort() {
    const modalRef = this.modalService.open(PriceSortModalComponent, {
      title: 'Urutkan',
      closeButton: true,
      data: {
        filter: this.filterPriceSort
      }
    });
    modalRef.onResult().subscribe((data) => {
      this.gaService.addEvent('Supplier_sort_byprice', 'supplier', '');
      this.page = 1;
      this.products = [];
      this.filterPriceSort = data;
      this.getListProduct(
        this.warehouseDetail.detailWarehouse.id,
        this.categoryId
      );
    });
  }

  categoryFilter() {
    const modalRef = this.modalService.open(CategoryModalComponent, {
      title: 'Kategori',
      closeButton: true,
      data: {
        nameLabel: this.nameLabel,
        categoryList: this.listCategorys
      }
    });
    modalRef.onResult().subscribe((data) => {
      this.gaService.addEvent('Supplier_product_category', 'supplier', '');
      this.page = 1;
      this.products = [];
      this.nameLabel = data.name;
      this.categoryId = data.id;
      this.location.replaceState(
        `/supplier/detail?ss=${this.supplierSlug}&category=${data.id}`
      );
      this.getListProduct(this.warehouseDetail.detailWarehouse.id, data.id);
    });
  }

  supplierInfo() {
    this.gaService.addEvent('Supplier_info', 'supplier', '');
    const modalRef = this.modalService.open(SupplierInfoModalComponent, {
      title: 'Informasi Supplier',
      closeButton: true,
      data: {
        supplierInfo: this.warehouseDetail.detailWarehouse.operationalHours,
        phoneSupplier: this.warehouseDetail.detailWarehouse.phone,
        emailSupplier: this.warehouseDetail.detailWarehouse.email,
        nameSupplier: this.warehouseDetail.detailWarehouse.name,
        imageSupplier: this.warehouseDetail.detailWarehouse.supplier.image,
        supplierId: this.warehouseDetail.detailWarehouse.id,
        minPurchase: this.warehouseDetail.detailWarehouse.minPurchase,
        tierImage: this.warehouseDetail.detailWarehouse.supplier.tier.image
      }
    });
    modalRef.onResult().subscribe(() => {
      // this.dataOperationaltMerge = data.supplierInfo;
    });
  }

  goToDetail(slug: string, variantSlugDetail: string, warehouseId: string) {
    this.router.navigate([`product/detail/${slug}`], {
      queryParams: {
        variantSlugDetail: variantSlugDetail,
        warehouseId: warehouseId
      }
    });
  }

  onScroll() {
    this.page = this.page + 1;
    if (this.canLoad) {
      this.getListProduct(
        this.warehouseDetail.detailWarehouse.id,
        this.categoryId
      );
    }
  }

  goToHomePage() {
    this.router.navigate(['/'], { replaceUrl: true });
  }

  goToAddressList() {
    this.router.navigate(['/address/list']);
  }

  toggleShowFullText() {
    this.isHidden = !this.isHidden;
  }

  handleTabView(data: string) {
    const category =
      this.route.snapshot?.queryParamMap.get('category') || this.categoryId;
    if (data === 'beranda') {
      this.products = [];
      this.page = 1;
      this.beranda = true;
      this.promo = false;
      this.isLoading = false;
      this.canLoad = true;
      this.location.replaceState(
        `/supplier/detail?ss=${this.supplierSlug}&category=${
          this.categoryId ? this.categoryId : ''
        }`
      );
      this.getListProduct(this.warehouseDetail.detailWarehouse.id, category);
    } else if (data === 'promo') {
      this.products = [];
      this.page = 1;
      this.beranda = false;
      this.promo = true;
      this.isLoading = false;
      this.canLoad = true;
      this.location.replaceState(`/supplier/detail?ss=${this.supplierSlug}`);
      this.filterPriceSort = 'lowerPrice';
      this.getListProduct(this.warehouseDetail.detailWarehouse.id);
    }
  }
}
