import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SupplierListComponent } from './supplier-list/supplier-list.component';
import { SupplierDetailComponent } from './supplier-detail/supplier-detail.component';
import { HomeContainerComponent } from '../../shared/layout/container/home-container/home-container.component';
import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';
import { CoverageAreaComponent } from './coverage-area/coverage-area.component';
import { DetailSupplierResolver } from '../../shared/resolvers/detail-supplier.resolver';

const routes: Routes = [
  {
    path: '',
    component: HomeContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        path: 'list',
        component: SupplierListComponent,
        title: 'Official-Partner/List'
      }
    ]
  },
  {
    path: '',
    component: BasicContainerComponent,
    children: [
      {
        path: 'cangkupan-area/:id',
        component: CoverageAreaComponent,
        title: 'Cakupan-Area',
        data: {
          pageTitle: "Cakupan Area",
        }
      }
    ]
  },
  {
    path: 'detail',
    component: SupplierDetailComponent,
    title: 'Supplier',
    resolve: {
      meta: DetailSupplierResolver
    },
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SupplierListRoutingModule {}
