import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { PartnerService, ListPartnerAllDto, Warehouse, ResponseListPartnerAllDto } from '@youtap-web-lib';
import { TransferStateService } from '../../../shared/service/transfer-state.service';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';

const STATE_KEY_OFFICIAL_PARTNER = 'dataOfficialPartner';

@Component({
  selector: 'youtap-bos-supplier-list',
  templateUrl: './supplier-list.component.html'
})
export class SupplierListComponent implements OnInit {
  officialPartners!: Warehouse[];

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private partnerService: PartnerService,
    private transferStateService: TransferStateService
  ) {}

  ngOnInit(): void {
    this.getListAllPartner();
  }

  fetchDataListAllPartner(isServer = false) {
    const data: ListPartnerAllDto = {
      listOfficialPartnerInput: {
        limit: 20,
        page: 1
      }
    };

    this.partnerService.listOfficialPartner(data).subscribe({
      next: ({ data }) => {
        if(isServer) {
          this.transferStateService.set(STATE_KEY_OFFICIAL_PARTNER, data);
        }
        this.officialPartners = data.listOfficialPartner.officialPartners;
      }
    });
  }

  getListAllPartner() {
    if(isPlatformServer(this.platformId)){
      this.fetchDataListAllPartner(true);
    }
    if(isPlatformBrowser(this.platformId)){
      const data = this.transferStateService.get<ResponseListPartnerAllDto>(STATE_KEY_OFFICIAL_PARTNER, {} as ResponseListPartnerAllDto);
      if(data && data.listOfficialPartner) {
        this.officialPartners = data.listOfficialPartner.officialPartners;
      }
      else {
        this.fetchDataListAllPartner();
      }
    }
  }
}
