import {Injectable} from "@angular/core";

export interface supplierStateDto {
  page: number,
  supplierSlug: string
}

@Injectable({
  providedIn: "root"
})
export class ScrollData {
  supplierState!: supplierStateDto

  set supplierStage(data: supplierStateDto) {
    this.supplierState = data
  }

  get supplierStage(): supplierStateDto {
    if (this.supplierState) {
      return this.supplierState
    } else {
      return {
        page: 0,
        supplierSlug: ""
      }
    }
  }

}
