import { Component, EventEmitter, Inject, Input, OnInit, Output, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalService } from '../../../service/modal/modal.service';
import { AuthData } from '../../../data/auth.data';
import { WindowActionService } from '../../../service/window-action.service';
import { CountInboxService } from '../../../service/count-inbox.service';
import { CountCartService } from '../../../service/count-cart.service';
import { HeaderModalComponent } from 'apps/youtap-bos/src/app/main/home/header-modal/header-modal.component';
import { EmptyAddressModalComponent } from 'apps/youtap-bos/src/app/main/home/empty-address-modal/empty-address-modal.component';
import { ListAddressModalComponent } from 'apps/youtap-bos/src/app/main/home/list-address-modal/list-address-modal.component';
import { Location, isPlatformBrowser } from '@angular/common';

@Component({
  selector: 'supplier-header',
  templateUrl: './supplier-header.component.html'
})
export class SupplierHeaderComponent implements OnInit {
  cartItem = '0';
  coverageArea = 'Masukkan Alamat';
  showModal = false;
  token = '';
  isReady = false;

  @Input() slug = '';
  @Input() supplierSlug = '';
  @Input() searchValue = '';
  @Output() searchChange = new EventEmitter<string>();

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private router: Router,
    private modalService: ModalService,
    private activatedRouted: ActivatedRoute,
    private location: Location,
    public authData: AuthData,
    private windowActionService: WindowActionService,
    public countInboxService: CountInboxService,
    public countCartService: CountCartService
  ) {}

  ngOnInit(): void {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
    this.token = this.authData.token;

    if (Object.keys(this.authData.location).length) {
      this.coverageArea =
        this.authData.location.district.name +
        ',' +
        this.authData.location.regency.name;
    }
  }

  toLoginPage() {
    this.router.navigate(['/login'], {});
  }

  toCart() {
    this.router.navigate(['/cart/list'], {});
  }

  toInbox() {
    this.router.navigate(['/inbox'], {});
  }

  toProfile() {
    this.router.navigate(['/profile/dashboard'], {});
  }

  setLocation() {
    if (this.token) {
      this.router.navigate(['/address/list']);
    } else {
      this.headerModal();
    }
  }

  headerModal() {
    const modalRef = this.modalService.open(HeaderModalComponent, {
      title: 'Kirim belanja stok kemana?',
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      if (closed === 'location') {
        this.router.navigate(['/location/set'], {});
      } else if (closed === 'login') {
        this.router.navigate(['/login']);
      }
    });
  }

  emptyAddressModal() {
    const modalRef = this.modalService.open(EmptyAddressModalComponent);
    modalRef.onResult().subscribe((button) => {
      switch (button) {
        case 'add':
          this.router.navigate(['/address/add'], {});
          break;
        case 'skip':
          break;
        default:
          break;
      }
    });
  }

  listAddressModal() {
    const modalRef = this.modalService.open(ListAddressModalComponent);
    modalRef.onResult().subscribe((button) => {
      switch (button) {
        case 'use':
          setTimeout(() => {
            this.windowActionService.reload();
          }, 500);
          break;
        case 'add':
          this.router.navigate(['/address/add'], {});
          break;
        default:
          break;
      }
    });
  }

  goBack() {
    if (window.history.length < 3) {
      this.router.navigate(['/']);
    } else {
      this.location.back();
    }
  }

  search() {
    this.searchChange.emit(this.searchValue);
    if(this.supplierSlug) {
      this.location.replaceState(
        `/supplier/detail?ss=${this.supplierSlug}&search=${this.searchValue}`
      );
    }
    else {
      this.location.replaceState(
        `/supplier/detail?s=${this.slug}&search=${this.searchValue}`
      );
    }
  }
}
