import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { MetaTagInterface } from '../service/meta.service';
import { DetailWarehouseInput, WarehouseService } from '@youtap-web-lib';
import { isPlatformServer } from '@angular/common';

@Injectable({ providedIn: 'root' })
export class DetailSupplierResolver implements Resolve<MetaTagInterface> {
  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private warehouseService: WarehouseService
  ) {}

  resolve(
    route: ActivatedRouteSnapshot
  ):
    | Observable<MetaTagInterface>
    | Promise<MetaTagInterface>
    | MetaTagInterface {
    const slug = route.queryParamMap.get('s');
    const supplierSlug = route.queryParamMap.get('ss');
    {
      if (isPlatformServer(this.platformId)) {
        const data: DetailWarehouseInput = {
          detailWarehouseInput: {
            ...(slug && { slug: slug }),
            ...(supplierSlug && { supplierSlug: supplierSlug })
          }
        };

        return this.warehouseService
          .detailWarehouse(data)
          .toPromise()
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          .then(({ data }: any) => {
            const meta: MetaTagInterface = {
              title: `${data.detailWarehouse.supplier.name} - Produk Terlengkap Harga Kompetitif | Youtap BOS`,
              description: `Beli produk ${data.detailWarehouse.supplier.name} terlengkap dan harga kompetitif. Belanja Online Kebutuhan Usaha Anda hanya di Youtap BOS`,
              image: data.detailWarehouse.supplier.image
            };
            return meta;
          })
          .catch(() => {
            return {
              description: '',
              image: '',
              title: ''
            };
          });
      } else {
        return {
          description: '',
          image: '',
          title: ''
        };
      }
    }
  }
}
