import { Component } from '@angular/core';
import { Modal } from '../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../shared/service/modal/modal.service';

@Component({
  selector: 'price-sort-modal',
  templateUrl: './price-sort-modal.component.html'
})
export class PriceSortModalComponent extends Modal {
  title!: string;
  showButton = false;
  filter = 'lowerPrice';

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
    this.filter = inputs.data.filter;
  }

  select(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({ data: '1234' });
  }
}
