import {Injectable, Inject, PLATFORM_ID} from '@angular/core';
import {TransferState, makeStateKey} from '@angular/platform-browser';
import {isPlatformBrowser, isPlatformServer} from '@angular/common';

@Injectable({
  providedIn: 'root',
})
export class TransferStateService {
  constructor(
    private transferState: TransferState,
    @Inject(PLATFORM_ID) private platformId: object
  ) {
  }

  /**
   * Menyimpan data ke TransferState
   * @param key Key untuk menyimpan data
   * @param data Data yang akan disimpan
   */
  set<T>(key: string, data: T): void {
    const stateKey = makeStateKey<T>(key);
    if (isPlatformServer(this.platformId)) {
      this.transferState.set(stateKey, data);
    }
  }

  /**
   * Mengambil data dari TransferState
   * @param key Key untuk mengambil data
   * @param defaultValue Nilai default jika data tidak ada
   * @returns Data yang diambil dari TransferState atau nilai default
   */
  get<T>(key: string, defaultValue: T): T {
    const stateKey = makeStateKey<T>(key);
    if (isPlatformBrowser(this.platformId)) {
      const value = this.transferState.get(stateKey, defaultValue);
      // Hapus data dari TransferState setelah diambil di sisi browser
      this.transferState.remove(stateKey);
      return value;
    }
    return defaultValue;
  }

  /**
   * Mengecek apakah data sudah ada di TransferState
   * @param key Key untuk mengecek data
   * @returns Boolean apakah data sudah ada atau belum
   */
  hasKey<T>(key: string): boolean {
    const stateKey = makeStateKey<T>(key);
    return this.transferState.hasKey(stateKey);
  }
}
